import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { EssayQuestionComponent } from '@app/shared/question-components/essay-question/essay-question.component';
import { ListeningQuestionComponent } from '@app/shared/question-components/listening-question/listening-question.component';
import { ReadingQuestionComponent } from '@app/shared/question-components/reading-question/reading-question.component';
import { SpeakingQuestionComponent } from '@app/shared/question-components/speaking-question/speaking-question.component';
import { SummaryQuestionComponent } from '@app/shared/question-components/summary-question/summary-question.component';

@Component({
  selector: 'question-by-module',
  templateUrl: './question-by-module.component.html',
  styleUrls: ['./question-by-module.component.css']
})
export class QuestionByModuleComponent implements OnInit {
  @ViewChild(ListeningQuestionComponent, { static: false }) private listeningQuestion: ListeningQuestionComponent;
  @ViewChild(SpeakingQuestionComponent, { static: false }) private speakingQuestion: SpeakingQuestionComponent;
  @ViewChild(ReadingQuestionComponent, { static: false }) private readingQuestion: ReadingQuestionComponent;
  @ViewChild(EssayQuestionComponent, { static: false }) private essayWriting: EssayQuestionComponent;
  @ViewChild(SummaryQuestionComponent, { static: false }) private summaryWriting: SummaryQuestionComponent;


  @Input() moduleName: string;
  @Input() question: any;

  @Output() getEmitAnswer: any = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {

  }

  getAnswer(answer) {
    this.getEmitAnswer.emit(answer);
  }

  ngOnChanges(changes): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
  }

  onSubmit() {
    if (this.moduleName == 'Writing') {
      if (this.question.questionType == 'W001')
        this.essayWriting.onSubmit();
      else this.summaryWriting.onSubmit()
    }
    else if(this.moduleName=='Reading')
    this.readingQuestion.onSubmit();
    else if(this.moduleName=='Listening')
    this.listeningQuestion.onSubmit();
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    
  }
}
