import { Component, OnInit, Input } from "@angular/core";

import { CommonService } from "@core/http/serviceIndex";

@Component({
  selector: 'summary-score',
  templateUrl: './summary-score.component.html',
  styleUrls: ['./summary-score.component.css']
})
export class SummaryScoreComponent implements OnInit {

  @Input() scoreData;
  res: any;
  test =
    "<div>At the outset, there are numerous reasons why television has some negative aspects, but the most alarming one lies in the fact that <span class='marked'>wtching</span> television for longer hours makes children less productive, and this can be a contributing factor to less interest in social relation. For instance research in this area invariably shows that fifty percent of children who spent more time watching television <span class='marked'>doesnt</span> have good <span class='marked'>scial</span> relation <span class='marked'>in future</span>. Therefore, the adverse impact of over watching <span class='marked'>televsion</span> <span class='marked'>shold</span> not be <span class='marked'>ignred</span></div>";

  constructor(private _cservice: CommonService) { }

  ngOnInit() {
    setTimeout(() => {
      let summary = document.getElementById("summary"+ this.scoreData.summaryId);
      summary.append(
        this._cservice.stringToEl("<div>" + this.scoreData.yoursummary + "</div>")
      );

      if (this.scoreData.adviseContent) {
        let adviseContent = document.getElementById("essay-content" + this.scoreData.summaryId);
        adviseContent.append(this._cservice.stringToEl("<div>" + this.scoreData.adviseContent + "</div>"));
      }
    }, 100);

  }
}
