import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { SpeakingCode } from '@app/enum/app_enum';
import { AudioPlayComponent } from '@app/shared/basic-components/components/evaluate-question/audio-play/audio-play.component';
import { CommonService } from "@core/http/serviceIndex";

@Component({
  selector: 'speaking-score',
  templateUrl: './speaking-score.component.html',
  styleUrls: ['./speaking-score.component.css']
})
export class SpeakingScoreComponent implements OnInit {
  @ViewChild(AudioPlayComponent, { static: false }) private audioPlay: AudioPlayComponent;

  @Input() result;
  @Input() qType;

  public scorelists: any = [];
  public textLabel: string = '';
  BASE64_MARKER = ';base64,';

  constructor(private _service: CommonService) { }

  ngOnInit() {
    console.log(this.result)
    this.scorelists.push(this.result);
    // if (this.result.isNoEvaluate == false)
      setTimeout(() => {
        this.stringToEl();
      });
  }



  public isImgLoad: boolean;
  onImgLoad() {
    this.isImgLoad = false;
  }

  stringToEl() {
      let ques = document.getElementById("spoken-text" + this.result.questionId );
      if(ques)
      ques.append(
        this._service.stringToEl("<div>" + this.result.userSpeech + "</div>")
      );

  }



  playAnswer(){
    console.log('play answer')
  }
}
