import { Component, OnInit, Input } from '@angular/core';
import { ListeningCode } from '@app/enum/app_enum';
import { CommonService } from "@core/http/common.service";


@Component({
  selector: 'listening-score',
  templateUrl: './listening-score.component.html',
  styleUrls: ['./listening-score.component.css']
})
export class ListeningScoreComponent implements OnInit {
  public listeningcode=ListeningCode;
  
  @Input() data;

  constructor(private _cservice:CommonService) { }

  ngOnInit() {
    
  }

  ngOnChanges(changes): void {
    if (this.data.questionType == this.listeningcode.L001) {
      setTimeout(() => {
        let ques = document.getElementById("user-answer"+this.data.questionId);
        console.log(this._cservice.stringToEl("<div>" + this.data.userAnswer + "</div>"))
        ques.append(
          this._cservice.stringToEl("<div>" + this.data.userAnswer + "</div>")
        );
      });
    }
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
  }

}
