import { Component, OnInit, Input } from '@angular/core';
import { ReadingCode } from '@app/enum/app_enum';
import { CommonService } from "@core/http/common.service";
@Component({
  selector: 'reading-score',
  templateUrl: './reading-score.component.html',
  styleUrls: ['./reading-score.component.css']
})
export class ReadingScoreComponent implements OnInit {
  public readingCode=ReadingCode;
  
  @Input() result;
  public index:number=0;
  constructor(private _cservice: CommonService) { }

  ngOnInit() {
    console.log(this.result)
  }

  ngOnChanges(changes): void {

    if (this.result.questionType == this.readingCode.R005) {
      setTimeout(() => {
        let ques = document.getElementById("multi"+this.result.questionId);
        ques.append(
          this._cservice.stringToEl("<div>" + this.result.htmlquestion + "</div>")
        );
      });
    }else if(this.result.questionType==this.readingCode.R004){
      setTimeout(() => {
        let ques = document.getElementById("single"+this.result.questionId);
        ques.append(
          this._cservice.stringToEl("<div>" + this.result.htmlquestion + "</div>")
        );
      });
    }
  }

}
