import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";

import { EssayEvaluation, EssaySubStructure, EssaySubEvaluate, EssayDetail } from "@models/WritingModel";

@Component({
  selector: 'essay-question',
  templateUrl: './essay-question.component.html',
  styleUrls: ['./essay-question.component.css']
})
export class EssayQuestionComponent implements OnInit {

  @Output() submitEvaluation: any = new EventEmitter<any>();

  @Input() isNext: any;

  @Input() essayDetail:EssayDetail;



  evaluationForm: FormGroup;

  // array
  public versionList: any = ["American English", "British English"];

  // boolean
  public submitted: boolean = false;

  isLoading:boolean=false;

  constructor( private formBuilder: FormBuilder) { }

  ngOnInit() {
  }



  checkworkcount(val) {
    return val.length
  }

  onSubmit() {
    this.isLoading=true;
    this.submitted = true;
    if (this.evaluationForm.invalid) {
      return;
    } else {
      let evaluate = new EssayEvaluation;
      evaluate.version = this.evaluationForm.value.version;
      let intro = this.evaluationForm.value.userAnswer;
      evaluate.userAnswer = EssaySubEvaluate.create(intro, intro.length);

      // let body1 = this.evaluationForm.value.body1;
      // evaluate.body1 = EssaySubEvaluate.create(body1, body1.length);

      // let body2 = this.evaluationForm.value.body2;
      // evaluate.body2 = EssaySubEvaluate.create(body2, body2.length);

      // let conclusion = this.evaluationForm.value.conclusion;
      // evaluate.conclusion = EssaySubEvaluate.create(conclusion, conclusion.length);

      evaluate.essayId = this.essayDetail.essayId;
      evaluate.questionType=this.essayDetail.questionType;
      
      this.essayDetail=undefined;
      this.submitEvaluation.emit(evaluate);
    }
  }


  get f() {
    return this.evaluationForm.controls;
  }

 

  countWord(str) {
    return str.trim().replace(/\n/, "\n").split(/\s+/).length;
  }

  ngOnChanges(changes): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    this.evaluationForm=this.formBuilder.group(
      {
        version: ["American English", [Validators.required]],
        userAnswer: [""],

      });
      setTimeout(() => {
          this.isLoading=false;
      }, 100);
  }
  
}
