import { Component, OnInit, Input, Output, EventEmitter, OnDestroy, SimpleChanges } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Subscription } from 'rxjs';

import { CommonService } from "@core/http/serviceIndex";
import { SummaryDetail, SummaryEvaluation, SummaryWriting } from "@models/WritingModel";

@Component({
  selector: 'summary-question',
  templateUrl: './summary-question.component.html',
  styleUrls: ['./summary-question.component.css']
})
export class SummaryQuestionComponent implements OnInit {
  @Output() submitEvaluation: any = new EventEmitter<any>();
  @Input() isNext: any;

  @Input() summaryDetail: SummaryDetail;

  // any
  public singleSummary: any = {};

  // array
  public versionList: any = ["American English", "British English"];

  // boolean
  public submitted: boolean = false;
  isLoading:boolean=false;

  // public isStop:boolean=false;
  evaluationForm: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private _cservice: CommonService
  ) { }

  ngOnInit() {
   
  }

  
  onSubmit() {
    this.isLoading=true;
    this.submitted = true;
    if (this.evaluationForm.invalid) {
      return;
    } else {
      let evaluate = new SummaryEvaluation();
      evaluate.version = this.evaluationForm.value.version;
      evaluate.yoursummary = this.evaluationForm.value.summary;
      evaluate.totalWordCount = evaluate.yoursummary.length;
      evaluate.summaryId = this.summaryDetail.summaryId;
      evaluate.totalMinutes = 0;
      evaluate.questionType=this.summaryDetail.questionType;
      this.submitEvaluation.emit(evaluate);
    }
  }


  get f() {
    return this.evaluationForm.controls;
  }

  goBack() {
    this._cservice.goBackPrev();
  }


  countWord(str) {
    return str.trim().replace(/\n/, "\n").split(/\s+/).length;
  }



  ngOnChanges(changes: SimpleChanges): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.

 

    this.evaluationForm=this.formBuilder.group({
      version: ["American English", [Validators.required]],
      summary: [""],
    });

    setTimeout(() => {
      this.isLoading=false
    }, 100);
    setTimeout(() => {
      let summaryQuestion = document.getElementById("summary-question");
      if (summaryQuestion.childNodes[0])
        summaryQuestion.removeChild(summaryQuestion.childNodes[0]);
      summaryQuestion.append(
        this._cservice.stringToEl(
          "<div>" + this.summaryDetail.summaryQuestion + "</div>"
        )
      );
    }, 150);
  }

}
